var _this;
import {
	mapGetters
} from 'vuex'
import {
	goods
} from '@/config/mixins.js'
export default {
	mixins: [goods],
	data() {
		return {
			dataList: null,
			ins: 0,
			beans: [],
			advert: {},
			isChild: false
		};
	},
	computed: {
		cate_style() {
			return this.$store.state.config.cate_style ? this.$store.state.config.cate_style : 3;
		}
	},
	methods: {
		//切换样式 请求分类数据
		active(index) {
			this.ins = index;
			this.isChild = this.beans[index].hasOwnProperty('children');
		},
		categories() {
			this.$api.categories({}, res => {
				if (res.status) {
					for (var i = 0; i < res.data.length; i++) {
						if (i == 0) {
							res.data[i].active = true;
						}
					}
					this.beans = res.data;
					this.isChild = this.beans[0].hasOwnProperty('children');
				}
			});
		},
		goClass(cat_id) {
			uni.navigateTo({
				url: '/pages/classify/index?id=' + cat_id
			});
		},
		getBanner() {
			this.$api.advert({
				codes: 'tpl1_class_banner1'
			}, res => {
				this.advert = res.data.list;
			});
		},
		// 广告点击查看详情
		showSliderInfo(type, val) {
			if (type == 1) {
				// URL



			} else if (type == 2) {
				// 商品详情
				this.goodsDetail(val)
			} else if (type == 3) {
				// 文章详情
				this.$common.navigateTo('/pages/article/index?id=' + val + '&id_type=1')
			} else if (type == 4) {
				// 文章列表
				this.$common.navigateTo('/pages/article/list?cid=' + val)
			}
		},
	},
	onLoad() {
		this.categories();
		this.getBanner();
	},
};
